<template>
  <div :id="boxId"></div>
</template>
<script>
import Flowchart from "@/utils/showflow";
let { getFlow } = require("@/api/showflow").default;
export default {
  props: ["type"],
  mounted() {},
  data(){
   return {
     boxId:null,
   }
  },
  methods: {
    async init(wfe_id) {
      try {
      this.boxId =
        (Math.random() * 10000000).toString(16).substr(0, 4) +
        "-" +
        wfe_id +
        new Date().getTime() +
        "-" +
        wfe_id +
        Math.random()
          .toString()
          .substr(2, 5) +
        wfe_id;
        let res = await getFlow(wfe_id);
        if (res.data) {
          let type = this.type,
            options = {
              r: type == "pc" ? 30 : 20,
              startId: 1,
              endId: 9999,
              startColor: "#78c06e",
              endColor: "#00aaee",
              doingColor: "purple",
              doneColor: "#b9b9b9",
              waitColor: "#1e83d3",
              arrowColor: "#b9b9b9"
            };
          let style = {
            cssText: "border:1px solid #808080",
            width: type == "pc" ? 500 : 300,
            height: type == "pc" ? 500 : 300
          };
          let items = res.data.nodes.map(v => {
              return {
                id: v.nodeId,
                title: v.nodeName,
                text: v.users ? v.users.map(v => v.userName).join(",") : "",
                state: v.currentNode ? "doing" : v.finish ? "done" : "wait"
              };
            }),
            lines = res.data.line.map(v => {
              return {
                from: v.fromNodeId,
                to: v.toNodeId,
                id: v.lineId
              };
            });
          new Flowchart({
            options,
            items,
            lines,
            id: this.boxId,
            style
          });
        }
      } catch (err) {
        throw err;
      }
    }
  }
};
</script>
<style lang="less" scoped>
</style>

