import Methods from './methods/'
export default class Tree extends Methods {
    constructor({
        id = 'canvas',
        items = [],
        lines = [],
        options = {},
        style = {},
        methods={}
    }) {
        super()
        let Options = {
            startId:1,
            endId:999999999,
            type: '2d',
            startColor:'#78c06e',
            endColor:'#00aaee',
            doingColor:'purple',
            doneColor:'#b9b9b9',
            waitColor:'#1e83d3',
            arrowColor:'#b9b9b9',
            // 
            r: 30,
            // 
            rowSpace: 40,
            // 
            frame: 60,
        }, Style = {
            width: 500,
            height:500,
            cssText: null,
            lineWidth: 1,
            textBaseline: 'middle',
            textAlign: "center",
            textPadding:10,
            lineHeight:16,
            font: '12px arial'
        },Methods={
            hover:null,
            click:null,
            dbClick:null
        }
        Object.assign(Options, options)
        Object.assign(Style, style)
        Object.assign(Methods,methods)
        Object.assign(this, {
            id,
            DOM: document.getElementById(id),
            items,
            lines,
            options: Options,
            style: Style,
            methods:Methods,
            _lastTime: 0,
            hoverItem:null,
            newItems:[]
        })
        this.init()
    }
}