<template>
  <div style="height:100%;">
    <div class="Approval-dynamics-header">
      <div class="dynamics"></div>
      <a-button icon="table" @click="ifshowDiagram()" class="right showFlow" type="primary"></a-button>
    </div>
    <div class="scroll-outer-box" v-show="!this.showDiagram" style="height:calc(100vh - 100px);">
      <div class="flow-table" id="flow-table" style="height:100%;">
        <!-- <vuescroll :ops="ops" > -->
        <div class="scroll" :id="'scroll2'+wfeId">
          <a-timeline style="margin-top:10px;" >
            <div
              :class="{'over':item.nodeName==''} "
              v-for="(item,index) in processData"
              :key="index"
              class="line-box"
            >
              <a-timeline-item v-for="(val,i) in processData[index].users" :key="i" :class="{'newstyle':val.finish == false&&processData[index]&&processData[index-1]&& processData[index-1].users[0].finish !==processData[index].users[0].finish ,'approval-timeline':val.finish ==true,'newstyle-end':val.finish ==false}">
                <p>
                  <span>{{item.nodeName}}</span>
                  {{val.userName}}
                </p>
                 <p v-if="val.submitType ==2"></p>
                  <p v-if="val.submitType ==3" style="color:red;"></p>
                <p v-show="val.opinion">{{val.opinion}}</p>
                <p
                  v-show="val.submitTime !==null && val.submitTime !== undefined"
                >{{val.submitTime}}</p>
                <p>
                  
                  <a-switch
                    size="small"
                    checkedChildren=""
                    unCheckedChildren=""
                    :checked="val.finish"
                  />
                </p>
                <p v-show="val.opinionImg">
                  
                  <img :src="val.opinionImg" />
                </p>

                <div v-show="val.opinionImg">
                  <div class="btn-audio" style="display:inline-block;">
                    <audio controls="controls" preload :id="`${index}`+`${i}`" hidden ref="voice">
                      <source src="../../assets/images/song.mp3" type="audio/mpeg" /> audio 
                    </audio>
                    <div class="wifi-symbol-box" @click="bf(`${index}`+`${i}`,i,val)">
                      <!-- <div style=""> -->
                      <div class="trangel"></div>
                      <div class="trangel2"></div>
                      <div class="wifi-symbol" style="margin-left:10px;">
                        <div class="wifi-circle first"></div>
                        <div
                          class="wifi-circle second"
                          ref="secondActive"
                          :id="`second${index}`+`${i}`"
                        ></div>
                        <div
                          class="wifi-circle third"
                          ref="thirdActive"
                          :id="`third${index}`+`${i}`"
                        ></div>
                      </div>
                      <!-- </div> -->
                    </div>
                  </div>
                </div>
                <p v-show="val.opinionAutograph">
                  :
                  <img :src="val.opinionAutograph" />
                </p>
                  <i class="dash-line" v-if="processData[index+1] && processData[index].nodeId== processData[index+1].nodeId" ></i>
                <i class="solid-line" v-else ></i>
              </a-timeline-item>
            </div>
          </a-timeline>
        </div>
      </div>
    </div>

    <flow-chart
      ref="flowchart"
      v-show="showDiagram"
      type="tel"
      style="width: 100%;overflow:scroll"
      :showDiagram="showDiagram"
    ></flow-chart>
  </div>
</template>


