        //       opinionAutograph: "URL",
        //       flag:1
        //     },
        //     {
        //       userId: "13",
        //       userName: "",
        //       finish: true,
        //       submitTime: "2019-05-10 11:25:33",
        //       opinion: "",
        //       opinionImg: "URL",
        //       opinionVoice: "URL",
        //       voiceTime: "8000",
        //       opinionAutograph: "URL",
        //        flag:2
        //     },
        //     {
        //       userId: "14",
        //       userName: "",
        //       finish: true,
        //       submitTime: "2019-05-10 11:25:33",
        //       opinion: "",
        //       opinionImg: "URL",
        //       opinionVoice: "URL",
        //       voiceTime: "8000",
        //       opinionAutograph: "URL",
        //        flag:3
        //     }
        //   ]
        // },
        // {
        //   nodeId: 2,
        //   nodeName: "",
        //   users: [
        //     {
        //       userId: "12",
        //       userName: "1",
        //       finish: true,
        //       submitTime: "2019-05-10 11:25:33",
        //       opinion: "",
        //       opinionImg: "URL",
        //       opinionVoice: "URL",
        //       voiceTime: "8000",
        //       opinionAutograph: "URL",
        //        flag:1
        //     },
        //     {
        //       userId: "13",
        //       userName: "2",
        //       finish: true,
        //       submitTime: "2019-05-10 11:25:33",
        //       opinion: "",
        //       opinionImg: "URL",
        //       opinionVoice: "URL",
        //       voiceTime: "8000",
        //       opinionAutograph: "URL",
        //        flag:2
        //     },
        //     {
        //       userId: "14",
        //       userName: "3",
        //       finish: true,
        //       submitTime: "2019-05-10 11:25:33",
        //       opinion: "",
        //       opinionImg: "URL",
        //       opinionVoice: "URL",
        //       voiceTime: "8000",
        //       opinionAutograph: "URL"
        //     }
        //   ]
        // },
        // {
        // 	nodeId: 2,
        // 	nodeName: "--",
        // 	users: [
        // 	    {
        // 	        userId: "USERID",
        //               userName: "USERNAME",
        //               finish: false,
        //               submitTime: "SUBMITTIME",
        //               opinion: "",
        //               opinionImg: "OPINIONIMG",
        //               opinionVoice: "OPINIONVOICE",
        //               voiceTime: "VOICETIME",
        //               opinionAutograph: "OPINIONAUTOGRAPH"
        // 	    }
        // 	]
        // },
        // {
        // 	nodeId: 2,
        // 	nodeName: "--",
        // 	users: [
        // 	    {
        // 	        userId: "USERID",
        //               userName: "USERNAME",
        //               finish: false,
        //               submitTime: "SUBMITTIME",
        //               opinion: "",
        //               opinionImg: "OPINIONIMG",
        //               opinionVoice: "OPINIONVOICE",
        //               voiceTime: "VOICETIME",
        //               opinionAutograph: "OPINIONAUTOGRAPH"
        // 	    }
        // 	]
        // },
        // {
        // 	nodeId: 3,
        // 	nodeName: "",
        // 	users: [
        // 	    {
        // 	            userId: "USERID",
        //               userName: "USERNAME",
        //               finish: false,
        //               submitTime: "SUBMITTIME",
        //               opinion: "OPINION",
        //               opinionImg: "OPINIONIMG",
        //               opinionVoice: "OPINIONVOICE",
        //               voiceTime: "VOICETIME",
        //               opinionAutograph: "OPINIONAUTOGRAPH"
        // 	    }
        //   ],

        // }
      ]
    };
  },
  mounted() {
    console.log(this.wfeId);
    console.log(this.activeKey);
    new BScroll(`#scroll2${this.wfeId}`, {
      bounce: true,
      momentumLimitDistance: 5,
      scrollY: true,
      scrollbar: {
        fade: true,
        interactive: false // 1.8.0 
      },
      mouseWheel: true,
      freeScroll: true,
      click: true,
      taps: true
    });
    if(this.$route.name =='table'){
      this.init(this.wfeId )
    }else{
      this.init(this.$route.query.wfe_id )
    }
   
  },
  methods: {
    getHeight() {
      this.contentStyleObj.height = window.innerHeight - 70 + "px";
      // console.log(this.contentStyleObj.height);
    },
    ifshowDiagram() {
      // this.showDiagram = !this.showDiagram;
      // if(this.showDiagram)
      // this.$refs.flowchart.init();

       this.showDiagram = !this.showDiagram;
      if(this.showDiagram){
        // console.log(this.id);
        console.log(this.$route.name );
        if(this.$route.name =='table'){
          // console.log('table',this.id);
         this.$refs.flowchart.init(this.wfeId);
        
        }else{
         
        this.$refs.flowchart.init(this.$route.query.wfe_id );
        }
       
      }
      
    },
    init(wfe_id) {
      // console.log(this.showPC, "============yidongduan ");
      //  if(this.showPC){
      getWorkdynamic(wfe_id).then(res => {
        // console.log(res);
        this.processData = res.data;

        let datalist = this.processData;

        datalist.forEach((item, i) => {
          let users = item.users;
          users.map((val, index) => {
            //  console.log(val.submitTime);
            this.processData[i].users[index].submitTime = moment(
              item.users[index].submitTime
            ).format("YYYY-MM-DD HH:mm:ss");

            this.processData[i].users[index].voice = `${i}` + `${index}`;
          });
        });

        // console.log(this.processData, "========");
      });
      //  }
    },
   
    bf(id, i, val) {
      console.log(id, i);
      // console.log(i);
      let voiceTime = Number(val.voiceTime);
      this.audio = null;
      this.secondActive = null;
      this.thirdActive = null;
      console.log(this.$refs.voice);
      let voice = this.$refs.voice;
      voice.map((val, index) => {
        console.log(val.id);
        if (val.id == id) {
          // console.log(val);
          this.audio = val;
          if (this.audio !== null) {
            //.audio.paused false.
            //  alert(audio.paused);
            if (this.audio.paused) {
              this.audio.play(); //audio.play();// 
              //  this.bfclick = true
              this.bfclick = true;
            } else {
              this.audio.pause(); // 
              this.bfclick = false;
            }
            this.time = setTimeout(() => {
              console.log(this.secondActive, "=======time");
              console.log(this.audio.ended);
              if (this.audio.ended) {
                this.secondActive.className = " wifi-circle second";
                this.thirdActive.className = " wifi-circle third ";
                console.log(this.secondActive, "=======time");
              }
            }, voiceTime);
          }
        }
      });

      let second = this.$refs.secondActive;
      second.map((val, index) => {
        // console.log(val.id);
        let secondID = val.id.slice(6);
        let secondActive = val;
        if (id == secondID) {
          console.log(val);
          this.secondActive = val;
          if (this.bfclick) {
            secondActive.className = "second-active wifi-circle second";
          } else {
            secondActive.className = " wifi-circle second";
          }
        }
      });
      let third = this.$refs.thirdActive;
      third.map((val, index) => {
        let thirdID = val.id.slice(5);
        let thirdActive = val;
        if (id == thirdID) {
          console.log(val);
          this.thirdActive = val;
          if (this.bfclick) {
            thirdActive.className = "wifi-circle third third-active";
          } else {
            thirdActive.className = " wifi-circle third ";
          }
        }
      });
    }
  },
  watch: {
    showDiagram() {
      console.log("gaibiannpol");
      new BScroll(".scroll", {
        bounce: true,
        momentumLimitDistance: 5,
        scrollY: true,
        scrollbar: {
          fade: true,
          interactive: false // 1.8.0 
        },
        mouseWheel: true,
        freeScroll: true
      });
      //  new BScroll('.scroll-diagram',
      //         {
      //         bounce: true,
      //         momentumLimitDistance: 5,
      //         scrollY: true,
      //         scrollbar: {
      //             fade: true,
      //             interactive: false // 1.8.0 
      //         },
      //         mouseWheel: true,
      //         freeScroll:true
      //         }
      //         )
    }
