<style lang="less" scoped>
@import "../../assets/css/border.less";
.Approval-dynamics-header {
  width: 100%;
  height: 0.65rem;
  // background: #e0e5eb;
   border-bottom: 1px solid #e0e5eb;
  padding: 0 0.1rem;
  overflow: hidden;
  line-height: 0.65rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .showFlow {
    //   margin-top:0.06rem;
  }
  .dynamics {
    font-weight: bold;
    font-size: 0.16rem;
  }
}

.flow-table-diagram-position-box {
  width: 100%;
  height: 3rem;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
}
.flow-table {
  width: 100%;
  height: 3rem;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  table {
    width: 100%;
    tr:nth-of-type(n) {
      display: flex;
      td:nth-of-type(1) {
        width: 0.5rem;
        background: url("../../assets/images/dian_r_c.png") no-repeat;
        background-position: top;
      }
      td:nth-of-type(2) {
        //    border-bottom: 0.01rem solid #ccc;
        .border-1px(#ccc);
        flex: 1;
      }
    }
  }
}

