          no-repeat;
        background-position: center;
      }
      td:nth-of-type(2) {
        //    border-bottom: 0.01rem solid #ccc;
        .border-1px(#ccc);
        flex: 1;
      }
    }
    tr:last-child {
      display: flex;
      td:nth-of-type(1) {
        width: 0.5rem;
        background: url("../../assets/images/dian_r_c_sel.png") no-repeat;
        background-position: top;
      }
      td:nth-of-type(2) {
        //    border-bottom: 0.01rem solid #ccc;
        .border-1px(#ccc);
        flex: 1;
        p {
          color: #2db6ed;
        }
      }
    }
  }
}
p {
  margin: 0;
  padding: 0.05rem;
}
