.dash-line {
  display: inline-block;
  width: 100%;
  height: 1px;
  border-bottom: 1px dashed #ccc !important;
}
.solid-line{
  display: inline-block;
  width: 100%;
  height: 1px;
  border-bottom: 1px solid #ccc;
}
.line-box li:last-child i {
  border-bottom: 1px solid #ccc;
}

