  width: 100px;
  height: 30px;
  // background:url(images/oice_stop.png) no-repeat center bottom;
  // background: red;
  background-size: cover;
}
.box {
  width: 120px;
  height: 120px;
  box-sizing: border-box;
  position: relative;
  margin: 50px auto;
}
.wifi-symbol-box {
  position: relative;
  width: 100px;
  height: 30px;
  // background-color: #78c06e;
  overflow: hidden !important;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.wifi-symbol {
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  //   overflow: hidden !important;
  transform: rotate(135deg);
}
.wifi-circle {
  border: 2px solid #999999;
  border-radius: 50%;
  position: absolute;
}

.first {
  width: 2px;
  height: 2px;
  background: #cccccc;
  // top: 39px;
  // left: 39px;
  top: 25px;
  left: 25px;
}

.second {
  // width: 20px;
  // height: 20px;
  // top: 32px;
  // left: 32px;
  width: 15px;
  height: 15px;
  top: 20px;
  left: 20px;
  // animation: fadeInOut 1s infinite 0.2s;
}

.third {
  // width: 30px;
  // height: 30px;
  // top: 25px;
  // left: 25px;
  width: 25px;
  height: 25px;
  top: 15px;
  left: 15px;
  // animation: fadeInOut 1s infinite 0.4s;
}
.second-active {
  animation: fadeInOut 1s infinite 0.2s;
}

.third-active {
  animation: fadeInOut 1s infinite 0.4s;
}
@keyframes fadeInOut {
  0% {
    opacity: 0; /* 0*/
  }
  100% {
    opacity: 1; /* 1*/
  }
}
.trangel {
  width: 10px;
  height: 28px;
  transform: rotate(45deg);
  background: #fff;
  position: absolute;
  top: -4px;
  left: -5px;
  z-index: 222;
}
.trangel2 {
  width: 10px;
  height: 28px;
  transform: rotate(-45deg);
  background: #fff;
  position: absolute;
  top: 6px;
  left: -4px;
  z-index: 222;
}
</style>
