export default {
    draw() {
        let { width, height } = this.style
        this.ctx.clearRect(0, 0, width, height);
        this.drawLines()
        this.drawItems()
    },
    drawLines() {
        let { lines, newItems, ctx } = this
        lines.forEach(v => {
            let from = newItems.find(m => m.id == v.from),
                to = newItems.find(m => m.id == v.to),
                dots = this.getArrowDot(from.x, from.y, to.x, to.y)
            ctx.beginPath();
            ctx.moveTo(...dots[0])
            ctx.lineTo(...dots[1])
            ctx.stroke();
            this.drawArrow(...dots.flat())
        })
    },
    drawArrow(fromX, fromY, toX, toY) {
        let { ctx } = this,
            { arrowColor } = this.options,
            theta = 30,
            headlen = 10,
            angle = Math.atan2(fromY - toY, fromX - toX) * 180 / Math.PI,
            angle1 = (angle + theta) * Math.PI / 180,
            angle2 = (angle - theta) * Math.PI / 180,
            topX = headlen * Math.cos(angle1),
            topY = headlen * Math.sin(angle1),
            botX = headlen * Math.cos(angle2),
            botY = headlen * Math.sin(angle2);

        ctx.beginPath();
        ctx.fillStyle = arrowColor
        ctx.strokeStyle = arrowColor
        var arrowX = fromX - topX,
            arrowY = fromY - topY;

        ctx.moveTo(arrowX, arrowY);
        ctx.moveTo(fromX, fromY);
        ctx.lineTo(toX, toY);
        arrowX = toX + topX;
        arrowY = toY + topY;
        ctx.moveTo(arrowX, arrowY);
        ctx.lineTo(toX, toY);
        arrowX = toX + botX;
        arrowY = toY + botY;
        ctx.lineTo(arrowX, arrowY);
        ctx.closePath();
        ctx.fill()
        ctx.stroke();
    },
    drawItems() {
        let { newItems, ctx } = this,
            { r, startId, endId, startColor, endColor, doingColor, doneColor, waitColor } = this.options
        newItems.forEach(v => {
            let { x, y } = v
            ctx.beginPath();
            ctx.arc(x, y, r, 0, 2 * Math.PI);
            ctx.fillStyle = v.id == startId ? startColor : v.id == endId ? endColor : v.state == 'done' ? doneColor : v.state == 'doing' ? doingColor : waitColor
            ctx.fill()
            ctx.fillStyle = '#fff'
            ctx.textAlign = 'center'
            ctx.fillText(v.title, x, y)
        });
    },
    drawTip(item) {
        let { x, y, text } = item
        if (!text) return
        let { width, textPadding, lineHeight } = this.style,
            { r } = this.options,
            { ctx } = this,
            left = x > (width / 2),
            arrow = 5,
            maxWidth = Math.floor(left ? x : width - x) - 2 * arrow - 2 * r,
            strWidth = (text) => {
                return ctx.measureText(text).width
            },
            temp = '',
            row = [],
            textArr = text.split(''),
            lineWidth = maxWidth - textPadding * 2
        for (let i = 0; i < textArr.length; i++) {
            if (strWidth(temp) < lineWidth && strWidth(temp + textArr[i]) <= lineWidth) {
                temp += textArr[i]
            } else {
                row.push(temp)
                temp = textArr[i]
            }
        }
        row.push(temp)
        let height = (row.length * lineHeight) / 2 + textPadding
        // 
        ctx.beginPath();
        let x1 = (left ? -r : r) + x,
            arr = [
                [(left ? -(arrow + maxWidth) : (arrow + maxWidth)) + x1, y + height],
                [(left ? -arrow : arrow) + x1, y + height],
                [(left ? -arrow : arrow) + x1, y + arrow],
                [x1, y],
                [(left ? -arrow : arrow) + x1, y - arrow],
                [(left ? -arrow : arrow) + x1, y - height],
                [(left ? -(arrow + maxWidth) : (arrow + maxWidth)) + x1, y - height],
            ]
        arr.forEach((v, i) => {
            ctx[i == 0 ? 'moveTo' : 'lineTo'](...v)
        })
        ctx.closePath()
        ctx.strokeStyle = "#000"
        ctx.fillStyle = "#fff"
        ctx.fill()
        ctx.stroke()
        // 
        let startX = left ? (x1 - arrow - maxWidth + textPadding) : (x1 + arrow + textPadding),
            startY = y - height + textPadding + lineHeight / 2
        ctx.fillStyle = '#000'
        ctx.textAlign = 'left'
        for (let i = 0; i < row.length; i++) {
            let str = row[i]
            ctx.fillText(str, startX, startY + lineHeight * i)
        }
    },
    drawText(text, x, y, width, line = null) {
        if (!text) return;
        let { ctx, style } = this,
            arr = text.split(''),
            temp = "",
            row = [],
            lineHeight = 16,
            strWidth = (str) => {
                return ctx.measureText(str).width
            }
        ctx.fillStyle = style.fontColor;
        for (let i = 0; i < arr.length; i++) {
            if (strWidth(temp) < width && strWidth(temp + arr[i]) <= width) {
                temp += arr[i]
            } else {
                row.push(temp)
                temp = arr[i]
            }
        }
        row.push(temp)
        for (let i = 0; i < row.length; i++) {
            let str = row[i]
            if (i + 1 == line) {
                if (row[i + 1]) {
                    str = str.substring(0, str.length - 3) + '...'
                }
                return ctx.fillText(str, x, y + lineHeight * i)
            } else {
                ctx.fillText(str, x, y + lineHeight * i)
            }
        }
    },
    getArrowDot(x1, y1, x2, y2, r = this.options.r + 10) {
        // 
        let getDot = (x1, y1, x2, y2, k) => {
            if (k == -1)
                return console.warn('k!=-1')
            return [(x1 + k * x2) / (1 + k), (y1 + k * y2) / (1 + k)]
        },
            dis = this.getDistence(x1, y1, x2, y2),
            k1 = r / (dis - r),
            k2 = (dis - r) / r,
            dot1 = getDot(x1, y1, x2, y2, k1),
            dot2 = getDot(x1, y1, x2, y2, k2)
        return [dot1, dot2]
    },
    getDistence(x1, y1, x2, y2) {
        return Math.sqrt(Math.pow(Math.abs(x1 - x2), 2) + Math.pow(Math.abs(y1 - y2), 2))
    },
    isItem(x1, y1) {
        let { newItems } = this,
            { r } = this.options;
        return newItems.find(v => {
            let { x, y } = v
            return this.getDistence(x, y, x1, y1) <= r
        })
    }
}