<template>
  <div style="  height: 100%;">
    <!-- <a-layout id="components-layout-demo-custom-trigger"> -->
    <a-layout-content :style="{  padding: '0 24px 24px 24px', background: '#fff', height: '100%' }">
      <!-- <Workpc v-show="showPC" ref="pc"></Workpc>
      <Workmobile v-show="!showPC" ref="tel"></Workmobile> -->
      <Workpc v-if="showPC" ref="pc" :id='wfeId'></Workpc>
      <Workmobile v-else ref="tel" :wfeId='wfeId'></Workmobile>
    </a-layout-content>
    <!-- </a-layout> -->
  </div>
</template>


<script>
import Workpc from "./Work";
import Workmobile from "./Workmobile";
export default {
  props:[
    'wfeId'
  ],
  components: {
    Workpc,
    Workmobile
  },
  data() {
    return {
      showPC: true
      //  ifshowpc:null
    };
  },
  mounted() {
    //pc 
    this.isPc();
    console.log(this.wfeId);
  },
  methods: {
    isPc() {
      var sUserAgent = navigator.userAgent.toLowerCase();
      var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
      var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
      var bIsMidp = sUserAgent.match(/midp/i) == "midp";
      var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
      var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
      var bIsAndroid = sUserAgent.match(/android/i) == "android";
      var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
      var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
      let showPc = !(
        bIsIpad ||
        bIsIphoneOs ||
        bIsMidp ||
        bIsUc7 ||
        bIsUc ||
        bIsAndroid ||
        bIsCE ||
        bIsWM
      );
      this.showPC=showPc
    //  this.$refs[showPc?'pc':'tel'].init()
    //   this.$router.push(showPc ? "pc" : "mobile");

    if( this.showPC) {
      if(this.$route.name =='table'){
         this.$refs.pc.init(this.wfeId)  
      }else{
        this.$refs.pc.init(this.$route.query.wfe_id)  
      }
    }
  
    }
  }
};
</script>

<style lang="less" scoped>
#components-layout-demo-custom-trigger {
  height: 95%;
}
</style>


