export default {
    changePointer(e) {
        let cursor = 'default';
        if (this.isItem(...this.getPointerXY(e))) {
            cursor = 'pointer'
        }
        this.canvas.style.cursor = cursor;
    },
    onmousedown() { },
    onmouseup() { },
    onmouseout() { },
    onmousemove(e) {
        this.throttle(() => {
            this.changePointer(e)
            let { hover } = this.methods,
                { hoverItem } = this,
                item = this.isItem(...this.getPointerXY(e))
            if (item && !hoverItem) {
                this.hoverItem = item
                this.drawTip(item)
                if(hover)
                hover()
            } else if(!item && hoverItem) {
                this.hoverItem = null
                this.draw()
            }
        })
    },
    ondblclick() { }
}