                <!-- <div v-if="processData[index+1]">{{processData[index+1].users[0].opinion}}</div> -->
                <!-- <div>{{item.users[0].voice}}</div> -->
                <p><span>{{item.nodeName}} <span v-show="item.nodeName == ''">:</span> </span> {{val.userName}}</p>
                  <p v-if="val.submitType ==2"></p>
                  <p v-if="val.submitType ==3" style="color:red;"></p>
                <p v-show="val.opinion" style="display:flex; justify-content:space-between"> 
                  <span>{{val.opinion}}</span> 
                  <span>
                    <span v-show="val.submitTime !==null && val.submitTime !== undefined">{{val.submitTime}}</span>
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <span>&nbsp;&nbsp; 
