                <div v-show="val.opinionVoice" style="display:flex;align-items:center">
                  <div class="btn-audio" style="display:inline-block;line-height:36px">
                    <audio controls="controls" preload :id="`${index}`+`${i}`" hidden ref="voice">
                      <source :src="val.opinionVoice" type="audio/mpeg" /> audio 
                    </audio>
                    <div class="wifi-symbol-box" @click="bf(`${index}`+`${i}`,i,val)">
