                        <div class="wifi-circle first"></div>
                        <div class="wifi-circle second" ref="secondActive" :id="`second${index}`+`${i}`"></div>
                        <div class="wifi-circle third" ref="thirdActive" :id="`third${index}`+`${i}`"></div>
                      </div>
                    </div>
