          // console.log(val);
          this.audio = val;
          if (this.audio !== null) {
            //.audio.paused false.
            //  alert(audio.paused);
            if (this.audio.paused) {
              this.audio.play(); //audio.play();// 
              //  this.bfclick = true
              this.bfclick = true;
            } else {
              this.audio.pause(); // 
              this.bfclick = false;
            }
           this.time = setTimeout(() => {
              // console.log(this.secondActive, "=======time");
              console.log(this.audio.ended);
              if (this.audio.ended) {
                this.secondActive.className = " wifi-circle second";
                this.thirdActive.className = " wifi-circle third ";
                // console.log(this.secondActive, "=======time");
              }
            },voiceTime);
          }
        }
      });

