import mouse from './mouse-methods'
import canvas from './canvas'
export default class Methods {
    constructor() {
        Object.assign(this, mouse, canvas)
    }
    init() {
        this.initData()
        this.createCanvasDom()
        this.draw()
    }
    initData() {
        let { r, rowSpace } = this.options,
            { width, height } = this.style,
            { lines, items, newItems } = this,
            floors = [],
            format = () => {
                let len = items.length,
                    flat = floors.flat(),
                    temlen = flat.length,
                    lastIndex = floors.length - 1
                if (len <= temlen) return floors;
                // !
                floors.push(
                    items.filter(v => !flat.some(k => k.id == v.id))//
                        .filter(
                            v => lines.filter(
                                // 
                                m => floors[lastIndex].some(k => k.id == m.from)
                            ).some(n => n.to == v.id)
                        )
                )
                format()
            }
        //   
        floors.push(items.filter(v => lines.every(k => k.to != v.id)))
        format();
        let minHeight = rowSpace * (floors.length + 1) + (2 * r * floors.length)
        height < minHeight ? this.style.height = minHeight : ''
        floors.forEach((v, i) => {
            let y = rowSpace * (i + 1) + r * 2 * i + r,
                com = width / (v.length)
            v.forEach((m, n) => {
                let x = com / 2 + com * n
                newItems.push(Object.assign({ x, y,floor:i+1 }, items.find(v => v.id == m.id)))
            })
        });
    }
    createCanvasDom() {
        let { width, height, cssText, lineWidth, textBaseline, textAlign, font } = this.style,
            { type } = this.options,
            cid = this.createRandomId()
        this.DOM.innerHTML = `
        <canvas id="${cid}" width="${width}" height="${height}">
            <p>Canvas</p>
        </canvas>
        `
        let canvas = document.getElementById(cid)
        if (!canvas.getContext) {
            return console.error('Canvas!');
        }
        ['onmousedown', 'onmouseup', 'onmouseout', 'onmousemove', 'ondblclick'].forEach(v => {
            canvas[v] = this[v].bind(this)
        })
        canvas.style.cssText = cssText;
        this.canvas = canvas
        this.ctx = this.canvas.getContext(type)
        Object.assign(this.ctx, {
            lineWidth,
            textBaseline,
            font
        })
    }
    /**
     * @method
     * @description 
     * @param {*} e 
     */
    getPointerXY(e) {
        let { canvas } = this,
            x = e.pageX - canvas.offsetLeft,
            y = e.pageY - canvas.offsetTop;
        return [x, y]
    }
    /**
     * @method
     * @description id
     */
    createRandomId() {
        return (Math.random() * 10000000).toString(16).substr(0, 4) + '-' + (new Date()).getTime() + '-' + Math.random().toString().substr(2, 5);
    }
    /**
     * @method
     * @description 
     * @param {*} handler 
     * @param {*} wait 
     */
    throttle(handler, wait = 1000 / this.options.frame) {
        return (function () {
            var nowTime = new Date().getTime();
            if (nowTime - this._lastTime > wait) {
                handler.apply(this, arguments);
                this._lastTime = nowTime;
            }
        }).call(this)
    }
}